  //  
  const removeHabit = async (habitId) => {
    //  
    setHabits(habits.filter(habit => habit.id !== habitId));
    localStorage.setItem('habits', JSON.stringify(habits.filter(habit => habit.id !== habitId)));
    
    //     
    if (api.enabled && authToken) {
      try {
        console.log('    :', habitId);
        await api.deleteHabit(habitId, authToken);
        console.log('    ');
      } catch (error) {
        console.error('     :', error);
        //     -    
      }
    } else {
      console.log('    :', { apiEnabled: api.enabled, hasToken: !!authToken });
    }
  };

  //   
  const toggleHabitCompletion = (habitId, date) => {
    setHabits(habits.map(habit => {
      if (habit.id === habitId) {
        const dateKey = format(date, 'yyyy-MM-dd');
        const newCompletions = { ...habit.completions };
        
        if (newCompletions[dateKey]) {
          delete newCompletions[dateKey];
        } else {
          newCompletions[dateKey] = true;
        }
        
        //  
        const totalCompletions = Object.keys(newCompletions).length;
        const streak = calculateStreak(newCompletions);
        const bestStreak = Math.max(habit.bestStreak || 0, streak);
        
        return { 
          ...habit, 
          completions: newCompletions,
          totalCompletions,
          streak,
          bestStreak
        };
      }
      return habit;
    }));
  };

  //   
  const updateQuantityValue = (habitId, date, value) => {
    setHabits(habits.map(habit => {
      if (habit.id === habitId) {
        const dateKey = format(date, 'yyyy-MM-dd');
        const newValues = { ...habit.values };
        
        if (value === '' || value === 0) {
          delete newValues[dateKey];
        } else {
          newValues[dateKey] = parseFloat(value);
        }
        
        //  
        const totalCompletions = Object.keys(newValues).length;
        const streak = calculateStreak(newValues);
        const bestStreak = Math.max(habit.bestStreak, streak);
        
        return { 
          ...habit, 
          values: newValues,
          totalCompletions,
          streak,
          bestStreak
        };
      }
      return habit;
    }));
  };

  //  
  const updateMoodValue = (habitId, date, moodValue) => {
    setHabits(habits.map(habit => {
      if (habit.id === habitId) {
        const dateKey = format(date, 'yyyy-MM-dd');
        const newMood = { ...habit.mood };
        
        if (moodValue === null) {
          delete newMood[dateKey];
        } else {
          newMood[dateKey] = moodValue;
        }
        
        //  
        const totalCompletions = Object.keys(newMood).length;
        const streak = calculateStreak(newMood);
        const bestStreak = Math.max(habit.bestStreak, streak);
        
        return { 
          ...habit, 
          mood: newMood,
          totalCompletions,
          streak,
          bestStreak
        };
      }
      return habit;
    }));
  };

  //   
  const calculateStreak = (data) => {
    const sortedDates = Object.keys(data)
      .filter(date => data[date])
      .sort()
      .reverse();
    
    if (sortedDates.length === 0) return 0;
    
    let streak = 0;
    const today = new Date();
    
    for (let i = 0; i < sortedDates.length; i++) {
      const date = new Date(sortedDates[i]);
      const expectedDate = new Date(today);
      expectedDate.setDate(today.getDate() - i);
      
      if (isSameDay(date, expectedDate)) {
        streak++;
      } else {
        break;
      }
    }
    
    return streak;
  };

  //   
  const getHabitStats = (habit) => {
    const totalDays = differenceInDays(new Date(), new Date(habit.createdAt)) + 1;
    let completionRate = 0;
    
    if (habit.category === 'binary') {
      completionRate = totalDays > 0 ? Math.round((habit.totalCompletions / totalDays) * 100) : 0;
    } else if (habit.category === 'quantity') {
      const totalValue = Object.values(habit.values || {}).reduce((sum, val) => sum + val, 0);
      const targetValue = habit.target * totalDays;
      completionRate = targetValue > 0 ? Math.round((totalValue / targetValue) * 100) : 0;
    } else if (habit.category === 'mood') {
      const moodValues = Object.values(habit.mood || {});
      const avgMood = moodValues.length > 0 ? moodValues.reduce((sum, val) => sum + val, 0) / moodValues.length : 0;
      completionRate = Math.round(avgMood * 20); //  1-5  
    }
    
    return {
      totalDays,
      completionRate,
      streak: habit.streak,
      bestStreak: habit.bestStreak,
      totalCompletions: habit.totalCompletions
    };
  };

  //      
  const getHabitValue = (habit, date) => {
    const dateKey = format(date, 'yyyy-MM-dd');
    
    if (habit.category === 'binary') {
      return habit.completions[dateKey] || false;
    } else if (habit.category === 'quantity') {
      return habit.values[dateKey] || 0;
    } else if (habit.category === 'mood') {
      return habit.moods[dateKey] || null;
    }
    
    return false;
  };

  //    
  const getCategoryIcon = (category) => {
    switch (category) {
      case 'binary':
        return <Check size={14} />;
      case 'quantity':
        return <Activity size={14} />;
      case 'mood':
        return <Smile size={14} />;
      default:
        return <Zap size={14} />;
    }
  };

  //   
  const getCategoryName = (category) => {
    switch (category) {
      case 'binary':
        return '';
      case 'quantity':
        return '';
      case 'mood':
        return '';
      default:
        return '';
    }
  };

  //   uHabits
  const importFromUHabits = (data) => {
    try {
      const uhabitsData = JSON.parse(data);
      const importedHabits = [];
      
      if (uhabitsData.habits) {
        uhabitsData.habits.forEach(uhabit => {
          const habit = {
            id: Date.now() + Math.random(),
            name: uhabit.name || ' ',
            type: 'daily',
            category: 'binary',
            color: habitColors[Math.floor(Math.random() * habitColors.length)],
            target: 1,
            unit: '',
            reminderTime: '09:00',
            completed: {},
            values: {},
            mood: {},
            createdAt: new Date().toISOString(),
            streak: 0,
            bestStreak: 0,
            totalCompletions: 0
          };

          //   uHabits
          if (uhabit.entries) {
            uhabit.entries.forEach(entry => {
              const dateKey = format(parseISO(entry.date), 'yyyy-MM-dd');
              if (entry.value > 0) {
                habit.completions[dateKey] = true;
              }
            });
          }

          habit.totalCompletions = Object.keys(habit.completions).length;
          importedHabits.push(habit);
        });
      }

      setHabits([...habits, ...importedHabits]);
      setImportStatus(` ${importedHabits.length}   uHabits`);
      return true;
    } catch (error) {
      setImportStatus('    uHabits');
      return false;
    }
  };

  //   Loop Habit Tracker
  const importFromLoopHabit = (data) => {
    try {
      const loopData = JSON.parse(data);
      const importedHabits = [];
      
      if (loopData.habits) {
        loopData.habits.forEach(loopHabit => {
          const habit = {
            id: Date.now() + Math.random(),
            name: loopHabit.name || ' ',
            type: 'daily',
            category: 'binary',
            color: habitColors[Math.floor(Math.random() * habitColors.length)],
            target: 1,
            unit: '',
            reminderTime: '09:00',
            completed: {},
            values: {},
            mood: {},
            createdAt: new Date().toISOString(),
            streak: 0,
            bestStreak: 0,
            totalCompletions: 0
          };

          //   Loop Habit Tracker
          if (loopHabit.repetitions) {
            loopHabit.repetitions.forEach(rep => {
              const dateKey = format(parseISO(rep.timestamp), 'yyyy-MM-dd');
              habit.completions[dateKey] = true;
            });
          }

          habit.totalCompletions = Object.keys(habit.completions).length;
          importedHabits.push(habit);
        });
      }

      setHabits([...habits, ...importedHabits]);
      setImportStatus(` ${importedHabits.length}   Loop Habit Tracker`);
      return true;
    } catch (error) {
      setImportStatus('    Loop Habit Tracker');
      return false;
    }
  };

  //   CSV
  const importFromCSV = (data) => {
    try {
      const lines = data.split('\n');
      const importedHabits = [];
      const habitMap = new Map();

      //  CSV
      lines.forEach((line, index) => {
        if (index === 0) return; //  
        
        const columns = line.split(',');
        if (columns.length >= 3) {
          const habitName = columns[0].trim();
          const date = columns[1].trim();
          const value = columns[2].trim();

          if (!habitMap.has(habitName)) {
            const habit = {
              id: Date.now() + Math.random(),
              name: habitName,
              type: 'daily',
              category: 'binary',
              color: habitColors[Math.floor(Math.random() * habitColors.length)],
              target: 1,
              unit: '',
              reminderTime: '09:00',
              completed: {},
              values: {},
              mood: {},
              createdAt: new Date().toISOString(),
              streak: 0,
              bestStreak: 0,
              totalCompletions: 0
            };
            habitMap.set(habitName, habit);
          }

          const habit = habitMap.get(habitName);
          const dateKey = format(parseISO(date), 'yyyy-MM-dd');
          
          if (value === 'true' || value === '1' || value === 'yes') {
            habit.completions[dateKey] = true;
          }
        }
      });

      //  
      habitMap.forEach(habit => {
        habit.totalCompletions = Object.keys(habit.completions).length;
        importedHabits.push(habit);
      });

      setHabits([...habits, ...importedHabits]);
      setImportStatus(` ${importedHabits.length}   CSV`);
      return true;
    } catch (error) {
      setImportStatus('    CSV');
      return false;
    }
  };

  //   Daylio
  const importFromDaylio = (data) => {
    try {
      const lines = data.split('\n');
      const importedHabits = [];
      const moodHabit = {
        id: Date.now() + Math.random(),
        name: ' (Daylio)',
        type: 'daily',
        category: 'mood',
        color: '#667eea',
        target: 1,
        unit: '',
        reminderTime: '09:00',
        completed: {},
        values: {},
        mood: {},
        createdAt: new Date().toISOString(),
        streak: 0,
        bestStreak: 0,
        totalCompletions: 0
      };

      //  CSV Daylio
      lines.forEach((line, index) => {
        if (index === 0) return; //  
        
        const columns = line.split(',');
        if (columns.length >= 5) {
          const date = columns[0].trim();
          const mood = columns[4].trim();
          
          if (date && mood) {
            const dateKey = format(parseISO(date), 'yyyy-MM-dd');
            let moodValue = null;
            
            //   Daylio   
            switch (mood) {
              case 'awful':
                moodValue = 1;
                break;
              case 'bad':
                moodValue = 2;
                break;
              case 'meh':
                moodValue = 3;
                break;
              case 'good':
                moodValue = 4;
                break;
              case 'rad':
                moodValue = 5;
                break;
              default:
                moodValue = null;
            }
            
            if (moodValue !== null) {
              moodHabit.mood[dateKey] = moodValue;
            }
          }
        }
      });

      moodHabit.totalCompletions = Object.keys(moodHabit.mood).length;
      importedHabits.push(moodHabit);

      setHabits([...habits, ...importedHabits]);
      setImportStatus(` ${importedHabits.length}   Daylio`);
      return true;
    } catch (error) {
      setImportStatus('    Daylio');
      return false;
    }
  };

  //   Habit Tracker
