// API Configuration
const API_CONFIG = {
  //  development 
  development: {
    baseURL: 'http://localhost:5000/api',
    enabled: true //   
  },
  // Production Railway API
  production: {
    baseURL: 'https://habit-tracker-backend-production-d3e2.up.railway.app/api',
    enabled: true
  }
};

//   
const getCurrentEnvironment = () => {
  //    GitHub Pages
  if (window.location.hostname === 'dimkandin.github.io') {
    return 'production';
  }
  //   
  if (window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1') {
    return 'development';
  }
  //   production
  return 'production';
};

const currentEnv = getCurrentEnvironment();
const config = API_CONFIG[currentEnv];

console.log(` API Config: ${currentEnv}`, config);

// API 
export const api = {
  baseURL: config.baseURL,
  enabled: config.enabled,
  
  //   API
  async health() {
    if (!this.enabled) return { status: 'disabled' };
    try {
      const response = await fetch(`${this.baseURL}/health`);
      return await response.json();
    } catch (error) {
      console.error('API health check failed:', error);
      return { status: 'error', error: error.message };
    }
  },

  // 
  async register(email, password, name) {
    if (!this.enabled) throw new Error('API ');
    try {
      const response = await fetch(`${this.baseURL}/auth/register`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password, name })
      });
      
      const contentType = response.headers.get('content-type');
      if (!contentType || !contentType.includes('application/json')) {
        throw new Error('      ');
      }
      
      const data = await response.json();
      if (!response.ok) {
        throw new Error(data.error || ' ');
      }
      
      return data;
    } catch (error) {
      if (error.name === 'TypeError' || error.message.includes('Failed to fetch')) {
        throw new Error('    ');
      }
      throw error;
    }
  },

  async login(email, password) {
    if (!this.enabled) throw new Error('API ');
    try {
      const response = await fetch(`${this.baseURL}/auth/login`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email, password })
      });
      
      const contentType = response.headers.get('content-type');
      if (!contentType || !contentType.includes('application/json')) {
        throw new Error('      ');
      }
      
      const data = await response.json();
      if (!response.ok) {
        throw new Error(data.error || ' ');
      }
      
      return data;
    } catch (error) {
      if (error.name === 'TypeError' || error.message.includes('Failed to fetch')) {
        throw new Error('    ');
      }
      throw error;
    }
  },

  // 
  async getHabits(token) {
    if (!this.enabled) return [];
    const response = await fetch(`${this.baseURL}/habits`, {
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  async getCompletions(token) {
    if (!this.enabled) return [];
    console.log(' API getCompletions:', { url: `${this.baseURL}/habits/completions`, hasToken: !!token });
    const response = await fetch(`${this.baseURL}/habits/completions`, {
      headers: { 'Authorization': `Bearer ${token}` }
    });
    console.log(' getCompletions :', { status: response.status, ok: response.ok });
    if (!response.ok) {
      const errorText = await response.text();
      console.error(' getCompletions :', errorText);
      throw new Error('  ');
    }
    const result = await response.json();
    console.log(' getCompletions :', result.length, '');
    return result;
  },

  async getValues(token) {
    if (!this.enabled) return [];
    const response = await fetch(`${this.baseURL}/habits/values`, {
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  async getMoods(token) {
    if (!this.enabled) return [];
    const response = await fetch(`${this.baseURL}/habits/moods`, {
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  async createHabit(habit, token) {
    if (!this.enabled) throw new Error('API ');
    const response = await fetch(`${this.baseURL}/habits`, {
      method: 'POST',
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify(habit)
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  async updateHabit(id, habit, token) {
    if (!this.enabled) throw new Error('API ');
    const response = await fetch(`${this.baseURL}/habits/${id}`, {
      method: 'PUT',
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify(habit)
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  async deleteHabit(id, token) {
    if (!this.enabled) throw new Error('API ');
    const response = await fetch(`${this.baseURL}/habits/${id}`, {
      method: 'DELETE',
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  //  
  async toggleCompletion(habitId, date, completed, token) {
    if (!this.enabled) throw new Error('API ');
    console.log(' API toggleCompletion:', { habitId, date, completed, url: `${this.baseURL}/habits/${habitId}/completion` });
    const response = await fetch(`${this.baseURL}/habits/${habitId}/completion`, {
      method: 'POST',
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({ date, completed })
    });
    console.log(' API :', { status: response.status, ok: response.ok });
    if (!response.ok) {
      const errorText = await response.text();
      console.error(' API :', errorText);
      throw new Error('  ');
    }
    const result = await response.json();
    console.log(' API :', result);
    return result;
  },

  async setValue(habitId, date, value, token) {
    if (!this.enabled) throw new Error('API ');
    const response = await fetch(`${this.baseURL}/habits/${habitId}/value`, {
      method: 'POST',
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({ date, value })
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  },

  async setMood(habitId, date, moodValue, token) {
    if (!this.enabled) throw new Error('API ');
    const response = await fetch(`${this.baseURL}/habits/${habitId}/mood`, {
      method: 'POST',
      headers: { 
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${token}`
      },
      body: JSON.stringify({ date, mood_value: moodValue })
    });
    if (!response.ok) throw new Error('  ');
    return await response.json();
  }
};

export default api;